<template>
  <div class="content" >

      <div class="top">
        <router-link to="/idnex" tag="a">
          返回主页
       </router-link>
      </div>
       
      <div class="font">
          {{topicData.title}}
      </div>
    
    <div v-html="topicData.content" class="my-content" >
      

    </div>
    
  </div>
</template>

<script>

//   // 加载 axios 模块(用来 发送 ajax 的请求的)
//   import axios from 'axios';

// export default {
  
//     name: 'Content',
//     data () {

//       return {
//         topics:[],
        
//       }
//     },
//      // 钩子函数
//     mounted(){
//       var newId= this.$route.query.id;
//       axios.get('https://www.vue-js.com/api/v1/topic/' + newId).then((data)=>{
//         // 把获取到的数据 赋值给 topics
//         this.topics = data.data.data;
//         console.log(this.topics);



//       });
//       //实例化swiper对象
   
//     }
// }



// 引入请求数据的模块
import { getTopicShow } from "@/getdata/index.js"

export default {
  name: 'Content',
  data () {
    return {
      topicData:[]
    }
  },
  // 钩子函数
  mounted(){
    // 获取到 主题的id
    // console.log(this.$route.params.id);
    getTopicShow(this.$route.params.id).then((data)=>{
        this.topicData = data.data.data;
        console.log(data)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 // 加载基础的样式
  @import '../assets/scss/bass.scss';

  .top{
            width:100%;
            height:rem(100px);
            background:#eee;
            border:1px solid #e6e4e8;
            margin-bottom: rem(15px);
            a{
              display: inline-block;
             
              margin-left:rem(20px);
              color: #369219;
              font-size: rem(36px);
              line-height: rem(100px);
              text-align: center;
            }
        }
  .content{
     max-width: rem(750px);
     height:100%;
     position: relative;
     top:0;
     left:0;
     z-index: 9999;
     background:white;
     .font{
        color:#369219;
        font-size:rem(34px);
        line-height:rem(60px);
        margin:rem(10px) rem(20px);
        font-weight:bolder;


     }

  }
.my-content{
  width:100%;
  padding:0 rem(10px);
  box-sizing:content-box;

  
}

</style>



