<template>
  <div class="all">


      
      
      

      <!-- 子组件 -->
    <topic-list :topics="topics"></topic-list>

    <infinite-loading @infinite="infiniteHandler"></infinite-loading>
      
  </div>
</template>

<script>
// 引入请求数据的模块（引入的是某个对象所以加花括号）
import { getTopicList } from "@/getdata/index.js";

import TopicList from "@/components/topics/TopicList";

// 加载 上下滑动模块
import InfiniteLoading from 'vue-infinite-loading';
  export default {
   
    name: 'All',
    data () {

      return {
        topics:[],
         // 页面
         page:1,
        
      }
    },
      // 加载组件
    components:{
      TopicList,
       InfiniteLoading
    }, 
     // 钩子函数
    mounted(){
      getTopicList({
        limit:20,
      page:this.page
      }).then((data)=>{
      this.topics = data.data.data
      console.log(data.data.data)
    })
   
    },
    methods: {
    infiniteHandler($state) {
      setTimeout(() => {

        getTopicList({limit:10,page:++(this.page)}).then(data=>{
          // console.log(data) 
          this.topics = this.topics.concat(data.data.data);
        })

        // 重新请求数据
        $state.loaded();  
      }, 1000);
    },
  },


  }
</script>
<style lang="scss">

  
  // 加载基础的样式
  @import '../../assets/scss/bass.scss';


.all{
  margin-bottom: rem(150px);
}
  .all{
    margin-top: rem(80px);
  }
  /* 幻灯片 */
    .playstart{
    /*width: */
      position: fixed;
      top:rem(220px);
      width:100%;
      box-sizing: border-box;
      padding:rem(30px) rem(30px);
      height:rem(300px);
      background:#fff;
      box-sizing: border-box;
      
      .play{
            width:rem(700px);
            margin:0 auto;
            border-radius: rem(10px);
            height:100%;
            .swiper-container{
                width: 100%;
                height:100%;
                .swiper-slide-next{
                   width: 100%;
                    height:100%;
                }
              img{
                display: block;
                border-radius: rem(15px);
                width: 100%;
                height:100%;
                }
            }
            
      }
      

       
    }

    
  
 
</style>



