<template>
  <div class="topiclist">
    <ul class="items">

      <li v-for="(item,index) in topics" class="item">
        <router-link :to="{path:'/author',query:{loginname:item.author.loginname}}" tag="div" class="img">

          <img v-lazy="item.author.avatar_url" alt="" :title="item.author.loginname">

        </router-link>
        <div class="top" :class="{topStyle:item.top}">
          <span v-if="item.top">置顶</span>
          <span v-else-if="item.good">精华</span>  
          <span v-else>{{tab[item.tab]}}</span>      
        </div>
      
        <!-- <router-link :to="{path:'/index/content',query:{id:item.id}}" tag="div" class="title">{{item.title}}
 -->
          <router-link :to=" '/content/'+item.id  " tag="div" class="title">{{item.title}}</router-link>

        </router-link>
      </li>


    </ul>
  </div>
</template>

<script>
// 引入请求数据的模块
import { getTopicList } from "@/getdata/index.js";

export default {
  name: "TopicList",
  // 数据
  data() {
    return {
      tab: {
        weex: "weex",
        share: "分享",
        ask: "问答",
        job: "招聘"
      }
    };
  },

  // 钩子函数
  mounted() {
    // getTopicList().then((data)=>{
    //   this.topics = data.data.data
    // })
  },
  //接收  父组件传递过来的值
  props: ["topics"]
};
</script>
<style lang="scss">

@import "../../assets/scss/bass.scss";

.topiclist{
    margin-top:rem(80px);
}

 //加载的数据
  .items {
  width: 100%;
  margin-bottom:rem(145px);
  margin-top:rem(520px);
 
  .item {
      display: flex;
      // width:100%;
      height: rem(100px);
      border-bottom: 1px solid #ccc;
      padding: 0 rem(8px);
      box-sizing: border-box;
    .img {
      display: flex;
      img {
        display: block;
        align-self: center;
        width: rem(60px);
        height: rem(60px);
      }
    }
    .top {
      align-self: center;
      margin: 0 rem(15px);
      width: rem(65px);
      height: rem(40px);
      font-size: rem(22px);
      text-align: center;
      line-height: rem(40px);
      border-radius: rem(8px);

      background: #E5E5E5;
      color: #999;
    }
    .title {
      width: rem(550px);
      color: #333;
      font-size: rem(28px);
      line-height: rem(100px);
      // 处理文本超出容器,超出的部分用 ... 代替
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .topStyle{
        background: #369219;
        color: #fff;
      }
  }
}
</style>



