<template>
  <div class="me">
    <div class="me-top">设置</div>
    <div class="font-me">个人资料</div>
     <div class="font-me">账号管理</div>
      <div class="font-me">隐私设置</div>
      
  </div>
</template>

<script>
export default {
  name: 'Me',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>
<style scoped lang="scss">

  // 加载基础的样式
  @import '../assets/scss/bass.scss';
.me-top{
	width:100%;
	height:rem(120px);
	background:#121212;
	text-align: center;
	line-height: rem(120px);
	color:white;
	font-weight: bolder;
	font-size: rem(38px);

}

.font-me{
	width:100%;
	height: rem(120px);
	border-bottom: 2px solid #eee;
	margin-bottom: rem(15px);
	line-height: rem(120px);
	font-size: rem(32px);
	color:#414040;
	text-indent: rem(20px);
}
</style>



