<template>
  <div class="gzh">
   		<div class="bgimg">
   			<img src="../../static/images/4.jpg">
   		</div>

   		<div class="bookjia" v-for="(item,index) in topics">
   			<div>
   				<p >
   					 <img :src="item.author.avatar_url" alt="" :title="item.author.loginname">

   					 <span>{{item.author.loginname}}</span>                  
   				</p>
   				
   			</div>
   		</div>
  </div>
</template>

<script>
	// 引入请求数据的模块（引入的是某个对象所以加花括号）
import { getTopicList } from "@/getdata/index.js";
export default {
  name: 'Gzh',
  data () {
    return {
     topics:[],
    }
  },
   mounted(){
      getTopicList({
        limit:20,
      page:this.page
      }).then((data)=>{
      this.topics = data.data.data
      console.log(data.data.data)
    })
   
    },
}
</script>
<style scoped lang="scss">

  // 加载基础的样式
  @import '../assets/scss/bass.scss';
.gzh{
	margin-bottom: rem(200px);
	overflow: hidden;
}
.bgimg{
	width:100%;
	img{
		width:100%;
	}
}
.bookjia{
	

	div{
		float:left;
		width:50%;
		p{
			margin:rem(10px) rem(20px);
			text-align: center;
			img{
				width:100%;
				border:2px solid #eee;
			}

			span{
				font-size:rem(38px);
				line-height:rem(60px);
				color:#2b2b2b;
				font-weight: bolder;

			}

		}
	}
	
}

</style>



