<template>
  <div class="index">
   <!-- 顶部的导航 -->
    <nav class="top">
      <div class="All-top">
        <div class="font"><span class="fontcolor"> 小说</span></div>
        <div class="font"><span>漫画</span></div>
        <div class="search">

          <div class="search-child">
            <div>
               <span class="iconfont icon-sousuo"></span>
               <input type="text" name="" placeholder="书名、作者名、关键字">
            </div>
              
          </div>

        </div>
      </div>


 <!-- Swiper -->
      <div class="swiper-container">
        <div class="swiper-wrapper">
         

          <div class="swiper-slide">
                 <router-link to="/index" tag="a" :class=" (respath=='' || respath==undefined)?'itemStyle':'' ">全部</router-link>
          </div>
          <div class="swiper-slide">
                 <router-link to="/index/good" tag="a" :class=" respath=='good'?'itemStyle':'' ">精华</router-link>
          </div>
          <div class="swiper-slide">
                 <router-link to="/index/weex" tag="a" :class=" respath=='weex'?'itemStyle':'' ">weex</router-link>
          </div>
          <div class="swiper-slide">
                 <router-link to="/index/share" tag="a" :class=" respath=='share'?'itemStyle':'' ">分享</router-link>
          </div>
          <div class="swiper-slide">
                <router-link to="/index/ask" tag="a" :class=" respath=='ask'?'itemStyle':'' ">问答</router-link>
          </div>
          <div class="swiper-slide">
                 <router-link to="/index/job" tag="a" :class=" respath=='job'?'itemStyle':'' ">招聘</router-link>
          </div>
           <div class="swiper-slide">
                <router-link to="/index/ask" tag="a" :class=" respath=='ask'?'itemStyle':'' ">问答</router-link>
          </div>
          <div class="swiper-slide">
                <router-link to="/index/ask" tag="a" :class=" respath=='ask'?'itemStyle':'' ">问答</router-link>
          </div>
          <div class="swiper-slide">
                <router-link to="/index/ask" tag="a" :class=" respath=='ask'?'itemStyle':'' ">问答</router-link>
          </div>

          

          
        </div>
      </div>

    </nav>

<!-- 轮播图 -->
  <div class="wrap">
    <div id="lunbo">
      <!--设置显示的图片-->
      <ul class="img">
        <li style="display:block"><img src="../../static/images/0.jpg"></li>
        <li><img src="../../static/images/1.jpg"></li>
        <li><img src="../../static/images/2.jpg"></li>
        <li><img src="../../static/images/3.jpg"></li>
      </ul>
    </div>

  </div>
   

    <!-- 放子组件的视图 -->
    <router-view/> 


  </div>
</template>


<script>
export default {
  name: 'Index',
  // 计算属性
  computed:{
    // 计算路径
    respath:function(){
      // 把路径分割成数组
      var pathArr = this.$route.path.split('/');
      console.log(pathArr)
      // 返回 表示 一级路由的 str

      console.log(pathArr[2])

      return pathArr[2];
    }
  },
  // 钩子函数
  mounted(){

    // 自己的 js 就写在这里面了
    var swiper = new Swiper('.swiper-container', {
      slidesPerView: 6,
      spaceBetween: 20,
       pagination: {
          el: '.swiper-pagination',
          type: 'fraction',
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
    }); 



  var now=0;
  var len=$('#lunbo>.img>li').length;
  function run(){
    s=setInterval(function(){
      $('#lunbo>.img>li').hide();
      $('#lunbo>.num>li').css('background','#6699ff');
      if(now>=len-1){
        now=0;
      }else{
        now++;
      }

      $('#lunbo>.img>li:eq('+now+')').show();
      $('#lunbo>.num>li:eq('+now+')').css('background','#ff6699');
    },2000)
  }
  run();
  $('#lunbo').mouseover(function(){
    clearInterval(s);
    $('.left,.right').css('display','block');
  });
  $('#lunbo').mouseout(function(){
    run();
    $('.left,.right').css('display','none');
  })

  }
}
</script>
<style scoped lang="scss">

  // 加载基础的样式
  @import '../assets/scss/bass.scss';

  .All-top{
    position:fixed;
    top:0;
    left:0;
    padding:rem(35px);
    box-sizing: border-box;
    width:100%;
    height:rem(140px);
    background:#fff;
    border-bottom: 2px solid #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center;
    .font{
      width:rem(120px);
      height:100%;
      display: flex;
      justify-content: center;
       align-items: center;
       font-size: rem(32px);
       font-weight: bolder;
      
      span{
        display: inline-block;
        width:100%;
        line-height: rem(90px);
       
        text-align: center;
        
      }
      span.fontcolor{
        color:#034e94;
        border-bottom: rem(4px) solid #034e94;
      }
    }
    .search{
      flex:1;
      display: flex;
      height:100%;
     box-sizing: border-box;
     margin-left:rem(10px);
     justify-content: center;
    align-items: center;
    
      .search-child{
        width:100%;
        height:rem(90px);
        background: #f0f0f0;
        border-radius: rem(10px);
        
        display: flex;
        align-items: center;
        justify-content: center;
       div{
            align-self: center;
            width:rem(350px);

             span{
                font-size:rem(48px);
                color:#717072;
                line-height: rem(88px);
                margin-left: rem(10px);
                font-weight:bold;
            }
            input{
                border:none;
                background:#f0f0f0;
                font-size:rem(30px);
                color:#bbbbbb;
                width:rem(270px);
                line-height: rem(78px);
               
            }
       }
      }

    }
  }
.wrap{
        position: fixed;
        top:rem(210px);
        left:0;
        width:100%;

        background:white;
        #lunbo{
              width:rem(700px);
              height:rem(260px);
              
              margin:rem(20px) auto;
              position:relative;
            }
            #lunbo>.img>li{
                width:rem(700px);
              height:rem(260px);
                float:left;
                display:none;
                list-style:none;
            }
            #lunbo>.img>li img{
              width:100%;
              height:100%;
            }
 }
  // 导航
  nav.top{
    position: fixed;
    left:0;
    top: rem(140px);
    width: 100%;
    height: rem(80px);
    background:#F6F6F6;
    .swiper-container{
      .swiper-slide{
        font-size: rem(34px);
        text-align: center;

        a{
         
          line-height: rem(80px);
          color: #369219;
          padding: rem(3px) rem(10px);
          border-radius: rem(5px);
        }
        .itemStyle{
          background: #369219;
          color: #fff;          
        }
      }
    }
  }

</style>




