  @import './assets/scss/bass.scss';

 
 
  // 导航
 .footer{

  position: fixed;
  bottom: 0;
  left: 0;
  height:rem(144px);
  width:100%;
  background:#fff;
  border-top:2px solid #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  padding-top:rem(10px);
  .boton{
    
    
    flex:1;
    justify-content: center;
    text-align: center;
   
    color:#8d8d8d;
   
        li{

            span{
            font-size:rem(60px);
            cursor: pointer;
            text-align: center;
            cursor: pointer;

          }
          p{
