import Vue from 'vue'
import Router from 'vue-router'
import Me from '@/components/Me'
import Gzh from '@/components/Gzh'
import Index from '@/components/Index'
import Seach from '@/components/Seach'
// 加载主题相关组件


import All from '@/components/topics/All'
import Good from '@/components/topics/Good'
import Weex from '@/components/topics/Weex'
import Share from '@/components/topics/Share'
import Ask from '@/components/topics/Ask'
import Job from '@/components/topics/Job'
import content from '@/components/content'


import Author from '@/components/author'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
       children:[
        {
          path: '/',
          component: All,
        },
        {
          path: 'good',
          component: Good,
        },        
        {
          path: 'weex',
          component: Weex,
        },        
        {
          path: 'share',
          component: Share,
        },        
        {
          path: 'ask',
          component: Ask,
        },        
        {
          path: 'job',
          component: Job,
        }, 
             
        // {
        //   path: 'content',
        //   component: content,
        // },     
           
      ]
   
    },
    {
          path: '/content/:id',
          component: content
    } ,
     {
      path: '/gzh',
      name: 'gzh',
      component: Gzh,
   
    },
     {
      path: '/me',
      name: 'Me',
      component: Me,
   
    },
    {
      path: '/seach',
      name: 'Seach',
      component: Seach,
   
    },
    {
          path: '/author',
          component: Author,
    },   
    {
      path: '/*',
      redirect: '/index'
    }

  ]
})



