<template>
  <div class="author">
    <div class="author-wrap">
        <div class="top">
            <a href="/index">主页 /</a>
        </div>
        <div class="content">
          <p class="img"><img :src="topics.avatar_url" alt="" :title="topics.loginname"><span>{{topics.loginname}}</span></p>
          <p class="score"><span>{{topics.score}}积分</span></p>
          <p class="score">注册时间{{topics.create_at}}</p>
        </div>
    </div>
  </div>
</template>

<script>
 // 加载 axios 模块(用来 发送 ajax 的请求的)
  import axios from 'axios';

export default {
  
    name: 'Author',
    data () {

      return {
        topics:[],
        
      }
    },
     // 钩子函数
    mounted(){
      var loginname= this.$route.query.loginname;
     
      axios.get('https://www.vue-js.com/api/v1/user/'+loginname).then((data)=>{
        // 把获取到的数据 赋值给 topics
        this.topics = data.data.data;
        console.log(this.topics);
        var data=new Date();
        console.log(data.getFullYear());
          console.log(data.getDate());

      });
     
   
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  // 加载基础的样式
  @import '../assets/scss/bass.scss';


.author-wrap{
        .top{
            width:100%;
            height:rem(100px);
            background:#eee;
            border:1px solid #e6e4e8;
            a{
              display: inline-block;
              width:rem(120px);
              margin-left:rem(20px);
              color: #369219;
              font-size: rem(36px);
              line-height: rem(100px);
              text-align: center;
            }
        }
        .content{
          .img{
              height:rem(80px);

             
              img{
                  width:rem(60px);
                  height:rem(60px);
                   vertical-align: middle;
                   margin:0 rem(20px);
              }
              span{
                 
                  color: #778087;
                  font-size: rem(24px);
                  line-height: rem(80px);
              }
          }
        }
        .score{
             
              span{
                 font-size:rem(20px);
                 color:#363536;
                  margin:0 rem(20px);
                  line-height: rem(36px);
              }
        }

}

</style>



